import React, { lazy, useState, Suspense } from "react";
import { Route } from "react-router-dom";
import { Spinner } from "react-bootstrap";

const ForgetPasswordPage1 = lazy(() =>
  import("./forget-password-page1/forget-password-page1")
);
const ForgetPasswordPage2 = lazy(() =>
  import("./forget-password-page2/forget-password-page2")
);
const ForgetPasswordPage3 = lazy(() =>
  import("./forget-password-page3/forget-password-page3")
);

const ForgetPassword = ({ match: { url } }) => {
  const [email, setEmail] = useState("");
  const [id, setId] = useState("");
  const [isRightCode, setIsRightCode] = useState(false);
  const page1Props = { email, setEmail, setId };
  const page2Props = { email, setIsRightCode };
  const page3Props = { email, setEmail, id, setId };

  return (
    <Suspense fallback={<Spinner animation="border" />}>
      <Route
        exact
        path={`${url}/1`}
        render={(props) => <ForgetPasswordPage1 {...page1Props} {...props} />}
      />
      {id && (
        <Route
          exact
          path={`${url}/2`}
          render={(props) => <ForgetPasswordPage2 {...page2Props} {...props} />}
        />
      )}
      {isRightCode && (
        <Route
          exact
          path={`${url}/3`}
          render={(props) => <ForgetPasswordPage3 {...page3Props} {...props} />}
        />
      )}
      {/* {!id ? (
        <ForgetPasswordPage1 {...page1Props} />
      ) : (
        !isRightCode && <ForgetPasswordPage2 {...page2Props} />
      )}
      {isRightCode && <ForgetPasswordPage3 {...page3Props} />} */}
    </Suspense>
  );
};

export default ForgetPassword;
